import Vue from "vue";
import VueRouter from "vue-router";
/*  */
import Users from "@/Users/Users";
import Register from "@/Users/Register"; // 
import Login from "@/Users/Login"; // 
import Authorize from "@/Users/Authorize"; // 
/*  */
import AddDevice from "@/DeviceManager/AddDevice"; // 
import DeviceInformation from "@/DeviceManager/DeviceInformation"; // 
import EditDevice from "@/DeviceManager/EditDevice"; // 
import Code from "@/DeviceManager/Code"; // 
/*  */
import AddTask from "@/DeviceMaintenance/AddTask"; // 
import AddTaskInside from "@/DeviceMaintenance/AddTaskInside"; // 
import TaskInformation from "@/DeviceMaintenance/TaskInformation"; // 
import TaskDetailInfo from "@/DeviceMaintenance/TaskDetailInfo"; // 
import TssignTask from "@/DeviceMaintenance/TssignTask"; // 

const Home = () => import("../components/Home.vue");

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home,
    redirect: "/users",
    children: [
      /*  */
      {
        path: "/users",
        component: Users,
      },
      {
        path: "/register",
        component: Register,
      },
      {
        path: "/login",
        component: Login,
      },
      {
        path: "/authorize",
        component: Authorize,
      },
      /*  */
      // 
      {
        path: "/addDevice",
        component: AddDevice,
      },
      // 
      {
        path: "/deviceInformation",
        component: DeviceInformation,
      },
      // 
      {
        path: "/editDevice",
        component: EditDevice,
      },
      // 
      {
        path: "/code",
        component: Code,
      },
      /*  */
      // 
      {
        path: "/addTask",
        component: AddTask,
      },
      // 
      {
        path: "/addTaskInside",
        component: AddTaskInside,
      },
      // 
      {
        path: "/taskInformation",
        component: TaskInformation,
      },
      // 
      {
        path: "/taskDetailInfo",
        component: TaskDetailInfo,
      },
      // 
      {
        path: "/tssignTask",
        component: TssignTask,
      },
    ],
  },
];

const router = new VueRouter({
  routes,
});

export default router;
